.buttons_area {
    display: flex;
    gap: 45px;
    margin-top: 35px;
}

.button {
    color: white;
    font-family: inherit;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    cursor: pointer;
    width: 305px;
    height: 71px;
    border-radius: 10px;
    position: relative; 
    z-index: 1; /* Кнопки должны быть выше по слоям */
}

.button-video {
    background-color: #211F1F;
    padding: 22px 18px;
    border: solid 1px white;
}

.button-buy {
    background-color: #E76F04;
    border: none;
    padding: 22px 34px;
    }

@media (max-width:376px) {
    .buttons_area {
        flex-direction: column;
        gap: 20px;
        margin-top: 30px;
    }

    .button {
        width: 200px;
        height: 48px;
        font-size: 13px;
    }

    .button-video {
        background-color: #211F1F;
        padding: 15px 12.5px;
        border: solid 1px white;
    }
    
    .button-buy {
        background-color: #E76F04;
        border: none;
        padding: 15px 23px;
        }
}