.tariff_area {
    width: 412px;
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    gap: 60px;
}

.card_back-block {
    width: 100%;
    height: 140px;
    border-radius: 22px;
    display: flex;
    justify-content: center;
    padding-top: 25px;
}

.card-title {
    font-size: 36px;
    font-weight: 800;
    text-transform: uppercase;
}

.card_area {
    width: 100%;
    height: 580px;
    background-color: white;
    border-radius: 22px;
    color: black;
    font-size: 20px;
    position: absolute;
    z-index: 2;
    left: 0;
    top: 13%;
    padding: 26px 36px;
}

.card-list {
    display: flex;
    flex-direction: column;
    gap: 26px;
    align-items: start;
}

.card-item {
    font-family: 'Montserrat', sans-serif;
    text-align: left;
}

.icon {
    margin-right: 5px;
}

.icon-available {
    color: green;
    line-height: 1;
    font-size: 26px;
}

.card-price {
    font-family: 'Montserrat', sans-serif;
    margin-top: 60px;
    font-size: 36px;
    font-weight: 500;
}

.tariff-button {
    margin-top: 30px;
}